import React, {Component} from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ListGroup from 'react-bootstrap/ListGroup';
import axios from 'axios';
import {UpdateQuestionsState, Question} from '../App';
import config from '../config';
import QuestionScoresPanel from './QuestionScoresPanel';

type QuestionsPanelProps = {
  hash: string,
  updateState: (state: UpdateQuestionsState) => void,
  questions: Question[]
}

type QuestionsPanelState = {
}

export default class QuestionsPanel extends Component<QuestionsPanelProps, QuestionsPanelState> {
  async componentDidMount(): Promise<void> {
    try {
      const response = await axios.get(`${config.api_endpoint}/questions`);
      this.props.updateState({questions: response.data});
    } catch (e) {
      console.error(e);
    }
  }

  async componentDidUpdate(): Promise<void> {
    const {hash, questions, updateState} = this.props;
    const id: number = parseInt(hash.split('-')[1], 10);

    if (id) {
      const question = this.getQuestion();
      if (question && !question.scores) {
        try {
          const response = await axios.get(`${config.api_endpoint}/questions/${id}/scores`);
          const patchedQuestions = questions.map((question) => {
            if (question.id === id) {
              question.scores = response.data;
            }
            return question;
          });
          updateState({questions: patchedQuestions});
        } catch (e) {
          console.error(e);
        }
      }
    }
  }

  render() {
    return (
      <Container>
        <Row>
          <Col>
            {this.defineContent()}
          </Col>
        </Row>
      </Container>
    );
  }

  defineContent() {
    const id = this.getQuestionsId();

    if (id) {
      return <QuestionScoresPanel question={this.getQuestion()} />;
    }

    return (
      <>
        <h2 className="Scores-title">Valitse kysymys</h2>
        <ListGroup variant="flush">
          {this.renderQuestions()}
        </ListGroup>
      </>
    );
  }

  renderQuestions() {
    return this.props.questions.map((question) => {
      return (
        <ListGroup.Item key={question.id} action href={`#tulokset-${question.id}`}>
          {question.pluralText}
        </ListGroup.Item>
      );
    });
  }

  getQuestionsId(): number {
    return parseInt(this.props.hash.split('-')[1], 10);
  }

  getQuestion(): Question | undefined {
    return this.props.questions.find((question) => question.id === this.getQuestionsId());
  }
}
